import React from 'react';
import './App.css';

import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

import HomePage from './pages/HomePage';
import AboutPage from './pages/AboutPage';
import ArticleListPage from './pages/ArticlesListPage';
import ArticlePage from './pages/ArticlePage';
import NavBar from './NavBar';
import NotFoundPage from './pages/NotFoundPage';

function App() {
  return (
    <Router>
      <div className='App'>
        <NavBar />
        {/* Route take 2 main props (path) which specifies what url we want to render our component app (component) which specifies the actual component we want to render there */}
        <div id='page-body'>
          <Switch>
            <Route path='/' component={HomePage} exact />
            <Route path='/about' component={AboutPage} />
            <Route path='/articles-list' component={ArticleListPage} />
            <Route path='/article/:name' component={ArticlePage} />
            <Route component={NotFoundPage} />
          </Switch>
        </div>
      </div>
    </Router>
  );
}

export default App;
